* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #e9f0ef;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 20px;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.breed-select {
  width: 240px;
  height: 45px;
  background-color: #e9f0ef;
  border-radius: 20px;
  margin: 80px auto 40px;
  padding-left: 12px;
  font-size: 20px;
  font-weight: 500;
  display: block;
}

.loader {
  text-align: center;
  margin-top: 80px;
  margin-bottom: 40px;
}

.loader-span {
  width: 32px;
  height: 32px;
  border-top: 3px solid #212121;
  border-right: 3px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  max-width: 1240px;
  gap: 40px;
  margin: 0 auto;
  display: flex;
}

.cat-img, .cat-info-wrapper {
  width: 600px;
}

.cat-span {
  font-weight: 700;
}

